html,
body {
  width: 99%;
  height: 99%;
}

body {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
#root {
  display: none;
}
.background {
  display: block;
  width: 50%;
  height: 100%;
  position: absolute;
  top: 0;
  right: 0;
  background: linear-gradient(
    to right,
    rgba(133, 133, 133, 0) 0%,
    rgba(133, 133, 133, 0) 0%,
    rgb(43, 44, 54) 97%
  );
  z-index: 1;
}
.main{
  z-index: 99;
}
.container {
  z-index: 99;
  border-width: 1px;
  border-style: solid;
  border-image: linear-gradient(to bottom, #18181800, #eeeeee71, #18181800) 1 1;
  
}
.form{
  border-width: 1px;
  border-style: solid;
  border-image: linear-gradient(to right, #18181800, #eeeeee71, #18181800) 1 1;
  padding: 1.3vh 1.3vh;
}
.Title {
  color: #09f5c2;
  text-shadow: 0px 0px 20px #00ffc8;
  text-align: center;
  font-size: 2vh;
  margin: 2vh;
  font-family: "Teko", sans-serif;
}
.inner {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.display {
  color: white;
  margin: 14px;
  text-align: center;
  margin-bottom: 20px;
  width: 75%;
  height: 2.3vh;
  font-size: 3vh;
  font-family: "Lexend", sans-serif;
}
.keypad {
  display: grid;
  grid-template-columns: repeat(3, 7.2vh);
  grid-template-rows: repeat(4, 7.2vh);
  gap: 10px;
}
.keypad button {
  border: none;
  outline: none;
  cursor: pointer;
  font-size: 2.4vh;
  color: white;
  border-radius: 3px;
  font-family: "Lexend", sans-serif;
  border: 1px solid rgb(194, 194, 194);
  background: rgba(160, 160, 160, 0.515);
  transition: 0.3s;
}
.keypad button:hover {
  border: 1px solid #08c098e0;
  background: #148f7477;
  color: white; /* White text color for the number '1' */
  background-image: radial-gradient(circle, rgba(255, 255, 255, 0.3), rgba(255, 255, 255, 0) 44%)
}

/* sssssssss */
.line {
  content: "";
  height: 1px;
  width: 24vh;
  display: block;
  border-radius: 50%;
  align-content: center;
  background: rgb(255, 255, 255);
  background: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0.014443277310924318) 0%,
    rgba(140, 140, 140, 1) 33%,
    rgba(181, 181, 181, 1) 66%,
    rgba(255, 255, 255, 0) 100%
  );
  box-shadow: 0px 5px 30px #02d1ff, 0px -5px 30px #02d1ff;
}
